import { mapState, mapMutations } from 'vuex';
export default {
	data() {
		return {
		}
	},
	computed:{
		...mapState(["currentAddress", "devices"])
	},
	onLoad() {
		uni.setNavigationBarTitle({
		　　title:'智能硬件'
		})
	},
	mounted(){
		
	},
	methods: {
		...mapMutations(["setSelectDevice"]),
		switchAddress(){
			uni.navigateTo({
				url:'../address/addressList'
			})
		},
		addDevice(){
			uni.navigateTo({
				url:'./addDevice'
			})
		},
		selectDevice(device){
			this.$store.commit("setSelectDevice", device);
			uni.navigateTo({
				url:'./deviceDetail'
			})
		},
		configDevice(device){
			this.$store.commit("setSelectDevice", device);
			uni.navigateTo({
				url:'./configDevice'
			})
		}
	}
}
